.product-card {
  width: 25%;
  border: 1px solid rgb(201, 201, 201);
  border-radius: 15px;
  margin: 1px;
}
.product-card .card-info {
  padding: 15px;
  text-align: center;
}
.product-card .card-info .title {
  text-align: center;
  font-weight: bold;
  color: brown;
}

.product-card .card-info .info {
  color: gray;
  font-size: 12px;
  margin: 10px;
}

.product-card .card-info button {
  background-color: brown;
  color: white;
  text-align: center;
  border-radius: 10px;
  padding: 5px 10px;
  margin-top: 20px;
  border: none;
}
.product-card .card-info button a {
  color: white;
  text-decoration: none;
}
.product-card .card-image {
  width: 100%;
  height: 250px;
}
.product-card .card-image img {
  width: 100%;
  height: 100%;
  border-top-right-radius: 15px;
  border-top-left-radius: 15px;
  object-fit: cover;
}
