.main-header {
  background-color: rgb(62 35 35);
  color: white;
  padding: 10px;
  display: flex;
  flex-direction: row;
  width: 100%;
  justify-content: center;
  align-items: center;
  /* height: 126px; */
  /* text-align: center; */
}

.main-header.logo {
  width: 20%;
}

.main-header.logo img {
  width: 100%;
  /* height: 50px; */
  object-fit: cover;
}
.main-header nav {
  width: 80%;
}
.main-header nav ul {
  list-style-type: none;
}
.main-header nav ul li {
  display: inline-block;
  padding-left: 90px;
}
.main-header nav ul li a {
  color: white;
  text-decoration: none;
}
.main-header nav ul li:hover {
  color: brown;
  cursor: pointer;
}
